/* Cores */
body {
  background: linear-gradient(90deg, rgba(222, 30, 64, 0.2) 0%, rgba(34, 78, 75, 0.2) 100%);
}

.container {
  width: 800px;
  height: 400px;
  font-family: "Open Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  background-color: #fff;
  margin: 0 auto;
  border-radius: 35px;
  display: flex;
  overflow: hidden;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.2509803922);
}
.container .first-column {
  max-width: 190px;
  width: 25%;
  padding: 20px 25px;
  text-align: center;
}
.container .second-column {
  background-color: #E7D6C6;
  width: 80%;
  padding: 20px 25px;
}
.container .second-column .row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.container .second-column .row .search-city {
  background-color: #fff;
  width: 200px;
  height: 33px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 15px;
}
.container .second-column .row .card {
  background-color: #fff;
  display: flex;
  border-radius: 25px;
  padding: 20px 20px 12px 0px;
  width: 150px;
  justify-content: space-around;
}
.container .second-column .row .card img {
  pointer-events: none;
}
.container .second-column .row .card .details {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.container .second-column .row .card .details p {
  margin: 0px;
  font-weight: 600;
}
.container .second-column .row .card .details .amount {
  font-size: 24px;
}